.navbar {
    background: -webkit-radial-gradient(rgb(95, 34, 34), rgb(49, 45, 35));
    background: -o-radial-gradient(rgb(95, 34, 34), rgb(49, 45, 35));
    background: radial-gradient(rgb(95, 34, 34), rgb(49, 45, 35));
    min-height: 80px;
    position: fixed;
    width: 100%;
    -webkit-box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: rgb(255, 255, 255);
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
    z-index: 99;
    white-space: nowrap;
}

.navbar ul {
    margin: 0;
    padding: 0;
}

.navbar li {
    display: inline-block;
    padding: 24px 0;
    margin: 10px;
    width: 33%;
    text-align: center;
}

.brand {
   font-size: 40px;
   font-weight: bold;
}


